import Vue from 'vue'
import Vuex from 'vuex'
// vuex store 
import storage from 'store/storage'
//  
import data_pool from 'store/data_pool'
// 
import api from 'store/api'
import common_list from 'bizStore/common_list'
Vue.use(Vuex)
export default new Vuex.Store({
  modules: {
    // 
    data_pool,
    // api
    api,
    // storage
    storage,
    // pages
    common_list
  },
  strict: process.env.NODE_ENV !== 'production'
})
