// mixin/Page
// request
import ZRequest from 'util/ZRequest'
import Vue from 'vue'
// , 
export default {
  inject: {
    '$page': {
      default: this
    }
  },
  methods: {
    $request (name, params = {}) {
      let $page = this.$page || this
      if ($page && $page.PAGE_INFO) {
        let promise = this.$store.dispatch(name, params)
        // 
        const CURRENT_REQUEST_TYPE = ZRequest.REQUESTS[name].type
        return promise.then(response => {
          // , , 
          let count = ($page.PAGE_INFO.init_requests[name] || ($page.PAGE_INFO.init_requests[name] = {})).count
          $page.PAGE_INFO.init_requests[name].count = count ? count + 1 : 1
          if (CURRENT_REQUEST_TYPE === ZRequest.LIST) {
            // uuid
            let data = response.results.map(item => item._uuid)
            Vue.set($page.PAGE_INFO.init_requests[name], 'data', data)
          } else if (CURRENT_REQUEST_TYPE === ZRequest.DETAIL) {
            Vue.set($page.PAGE_INFO.init_requests[name], 'data', response._uuid)
          }
          return response
        }).catch(error => {
          // , , 
          let count = ($page.PAGE_INFO.init_requests[name] || ($page.PAGE_INFO.init_requests[name] = {})).count
          $page.PAGE_INFO.init_requests[name].count = count ? count + 1 : 1
          return Promise.reject(error)
        })
      } else {
        return this.$store.dispatch(name, params)
      }
    }
  }
}
