<template>
  <div class="about-us">
    <div class="about-us-banner"></div>
    <div class="flex" style="background: #fff; padding-top: 40px;">
      <div class="aside mr50" style="width: 150px;">
        <div class="aside-item-wrapper">
          <div
            class="aside-item"
            @click="scrollTo('profile')"
            :class="{active: isActive('profile')}"
          ></div>
          <div
            class="aside-item"
            @click="scrollTo('council')"
            :class="{active: isActive('council')}"
          ></div>
          <div
            class="aside-item"
            @click="scrollTo('team')"
            :class="{active: isActive('team')}"
          ></div>
          <div
            class="aside-item"
            @click="scrollTo('volunteer')"
            :class="{active: isActive('volunteer')}"
          ></div>
          <div
            class="aside-item"
            @click="scrollTo('download')"
            :class="{active: isActive('download')}"
          ></div>
        </div>
      </div>
      <div class="content">
        <div class="about-us-item" ref="profile">
          <div class="title"></div>
          <div class="body">
            <p>20156</p>
            <p>
              <br>
              <br><br>
              <br>
              
            </p>
          </div>
        </div>
        <div class="about-us-item" ref="council">
          <div class="title"></div>
          <div class="body">
            <person-cell
              v-for="item in council"
              :key="gen_key(item)"
              :item="item"
            ></person-cell>
          </div>
        </div>
        <div class="about-us-item" ref="team">
          <div class="title"></div>
          <div class="body">
            <person-cell
              v-for="item in team"
              :key="gen_key(item)"
              :item="item"
            ></person-cell>
          </div>
        </div>
        <div class="about-us-item" ref="volunteer">
          <div class="title"></div>
          <div class="body">
            <person-cell
              v-for="item in volunteer"
              :key="gen_key(item)"
              :item="item"
            ></person-cell>
          </div>
        </div>
        <div class="about-us-item" ref="download">
          <div class="title"></div>
          <div class="body" style="padding-right: 200px;">
            <div class="download-wrapper">
              <el-row class="download-header">
                <el-col :span="8"></el-col>
                <el-col :span="16"></el-col>
              </el-row>
              <el-row class="download-body" v-for="item in downloads" :key="gen_key(item)">
                <el-col :span="8">{{item.year}}</el-col>
                <el-col :span="16">
                  <div class="download-btn" @click="downloadHandler(item.url)">
                    <i class="el-icon-download mr5"></i>
                    <span></span>
                  </div>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  debounce,
  result
} from 'lodash'
function download (url, filename = '') {
  // 
  var eleLink = document.createElement('a')
  eleLink.download = filename
  eleLink.style.display = 'none'
  // blob
  eleLink.href = url
  // 
  document.body.appendChild(eleLink)
  eleLink.click()
  // 
  document.body.removeChild(eleLink)
}
export default {
  data () {
    return {
      downloads: [
        // 
        { year: '2019', url: 'https://baidu.com' },
        { year: '2019', url: 'https://baidu.com' }
      ],
      council: [
        {
          photo: require('img/person/liangda.png'),
          name: '',
          job: '',
          desc: 'CEO CIO'
        },
        {
          photo: require('img/person/lijian.png'),
          name: '',
          job: '',
          desc: ''
        },
        {
          photo: require('img/person/zhouling.png'),
          name: '',
          job: '',
          desc: '    '
        },
        {
          photo: require('img/person/zhaojun.png'),
          name: '',
          job: '',
          desc: '2006 '
        },
        {
          photo: require('img/person/zhangjunhong.png'),
          name: '',
          job: '',
          desc: 'IT'
        },
        {
          photo: require('img/person/zhangbo.png'),
          name: '',
          job: '',
          desc: ''
        }
      ],
      team: [
        {
          photo: require('img/person/zhangbo.png'),
          name: '',
          job: '',
          desc: ''
        },
        {
          photo: require('img/person/huangxiaoting.png'),
          name: '',
          job: '',
          desc: ' '
        },
        {
          photo: require('img/person/wangweinan.png'),
          name: '',
          job: '',
          desc: 'NGONGO'
        },
        {
          photo: require('img/person/dongyu.png'),
          name: '',
          job: '',
          desc: ''
        }
      ],
      volunteer: [
        {
          photo: require('img/person/yanshuai.png'),
          name: '',
          job: '',
          desc: ''
        }
      ]
    }
  },
  computed: {
    // todo
  },
  created () {
    // todo
    this.scrollHandler = debounce((e) => {
      this.$forceUpdate()
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      this.scrollTop = scrollTop
    }, 30)
    window.addEventListener('scroll', this.scrollHandler)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.scrollHandler)
  },
  methods: {
    tabClickHandler () {
      // todo
    },
    isActive (id) {
      let top = result(this.$refs, `${id}.offsetTop`) - 130
      let height = result(this.$refs, `${id}.offsetHeight`)
      let scrollTop = this.scrollTop
      if (id === 'profile') {
        return scrollTop < top + height
      }
      return scrollTop >= top && scrollTop < top + height
    },
    scrollTo (id) {
      let top = result(this.$refs, `${id}.offsetTop`)
      top = top - 130
      // console.log('top', top)
      window.scrollTo(0, top)
    },
    downloadHandler (url) {
      download(url)
    }
  }
}
</script>
<style lang="scss">
.about-us {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  .about-us-banner {
    background-color: lightblue;
    width: 1200px;
    height: 300px;
    img { width: 100%; height: 300px; }
  }
  .content {
    background-color: #fff;
  }
  .aside-item-wrapper {
    position: sticky;
    top: $page-header-padding-top;
  }
  .aside-item {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 14px;
    border-top: 1px solid #dadada;
    border-left: 1px solid #dadada;
    border-right: 1px solid #dadada;
    cursor: pointer;
    user-select: none;
    &:last-child {
      border-bottom: 1px solid #dadada;
    }
    &.active,
    &:hover {
      color: $--color-primary;
      border-top-color: $--color-primary;
    }
  }
  .about-us-item {
    .title {
      font-size: 20px;
      color: #333;
      height: 40px;
      line-height: 40px;
      border-bottom: 2px solid #ccc;
    }
    .body {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
  .download-wrapper {
    border: 1px solid #dadada;
    padding: 0 20px;
    font-size: 14px;
    color: #666;
    .el-row {
      height: 40px;
      align-items: center;
      display: flex;
    }
    .download-header {
      font-weight: 700;
    }
    .download-body {
      font-size: 12px;
    }
    .download-btn {
      height: 20px;
      padding: 0 5px;
      display: flex;
      align-items: baseline;
      justify-content: center;
      font-size: 14px;
      width: 60px;
      color: $--color-primary;
      border: 1px solid $--color-primary;
    }
  }
}
</style>
