/**
 * @author  
 * @time    2019-05-27
 * 
 * vue-router, vue-routerv3.0.6
 *
 * :
 *   Vue.use(ForwardBackward)
 *
 * :
 * {
 *   forward (_from) {
 *     console.log(', ')
 *     console.log('from', _from)
 *   },
 *   backward (_from) {
 *     console.log('')
 *   }
 * }
 */
import zbKeepAlive from './zbKeepAlive'
let plugin = Object.create(null)
plugin.install = function (Vue) {
  let beforeState = null
  let pushState = window.history.pushState
  let isBack = false

  window.history.pushState = function () {
    let args = Array.prototype.slice.apply(arguments)
    pushState.apply(this, args)
    // 
    isBack = false
    beforeState = window.history.state
      ? parseFloat(window.history.state.key)
      : null
  }
  window.addEventListener('popstate', function (e) {
    // history.go/back/forwardhashchangepopstate
    let currentState = window.history.state
    if (currentState && currentState.key) {
      currentState = parseFloat(currentState.key)
    }
    if (!beforeState) {
      isBack = false
    } else if (beforeState && currentState) {
      isBack = beforeState > currentState
    } else if (beforeState && !currentState) {
      isBack = true
    }
    beforeState = currentState
  }, false)
  let callHook = function (hooks, context, args) {
    let i = 0
    let len = hooks.length
    for (; i < len; i += 1) {
      let hook = hooks[i]
      hook.apply(context, args)
    }
  }
  Vue.mixin({
    beforeRouteUpdate (_to, _from, next) {
      let that = this
      let args = [_from, true]
      let { forward, backward } = that.$options
      // popstate
      setTimeout(() => {
        if (isBack) {
          if (backward) {
            callHook(backward, that, args)
          }
        } else {
          if (forward) {
            callHook(forward, that, args)
          }
        }
      }, 0)
      next()
    },
    beforeRouteEnter (_to, _from, next) {
      next(vm => {
        let that = vm
        let args = [_from]
        let { forward, backward } = that.$options
        // popstate
        setTimeout(() => {
          if (isBack) {
            if (backward) {
              callHook(backward, that, args)
            }
          } else {
            if (forward) {
              callHook(forward, that, args)
            }
          }
        }, 0)
      })
    }
  })
  Vue.component(zbKeepAlive.name, zbKeepAlive)
  // created
  const strats = Vue.config.optionMergeStrategies
  strats.forward = strats.backward = strats.beforeRouteUpdate = strats.created
}
export default plugin
